<template>
    <!--获取佣金-->
    <div >
        <div  v-loading="loading">
            <el-table :data="datas.list" style="width: 100%" size="small">
                <el-table-column width="70" prop="order_id" label="订单ID"></el-table-column>

                <el-table-column width="140" prop="order_sn" label="订单编号"></el-table-column>

                <el-table-column label="消费者">
                    <template slot-scope="scope">
                        ID: {{scope.row.user.user_id}} | 昵称: {{scope.row.user.nickname}}
                    </template>
                </el-table-column>

                <el-table-column label="订单类型">
                    <template slot-scope="scope">
                        开通会员/续费会员
                    </template>
                </el-table-column>

                <el-table-column label="佣金">
                    <template slot-scope="scope">
                        ￥ {{scope.row.money}}
                    </template>
                </el-table-column>

                <el-table-column label="推广者">
                    <template slot-scope="scope">
                        ID: {{scope.row.puser.user_id}} | 昵称: {{scope.row.puser.nickname}}
                    </template>
                </el-table-column>

                <el-table-column label="受益者">
                    <template slot-scope="scope">
                        ID: {{scope.row.myuser.user_id}} | 昵称: {{scope.row.myuser.nickname}}
                    </template>
                </el-table-column>

                <el-table-column prop="add_time" label="分佣时间"></el-table-column>
            </el-table>
            <div style="text-align: right" class="block">
                <el-pagination @current-change="getList" :page-size="datas.limit"
                               layout="total, prev, pager, next" :total="datas.count">
                </el-pagination>
            </div>
        </div>
    </div>
</template>
<script>
    import * as api from "@/api";
    export default {
        name: "AgentCommission",
        props:['user_id'],
        data() {
            return {
                loading:false,
                datas:{},
            }
        },
        created(){
            this.getList();
        },
        methods: {
            getList(page=1){
                this.loading = true;
                api.post("/shop/admin.agent.agent/agentVip", {
                    appToken: this.$store.getters.appToken,
                    user_id:this.user_id,
                    page:page,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },
        },
    }
</script>
<style scoped>

</style>
