<template>
    <!--获取-->
    <div >
        <div  v-loading="loading">
            <el-table :data="datas.list" style="width: 100%" size="small">
                <el-table-column prop="order_id" label="订单ID"></el-table-column>

                <el-table-column prop="order_sn" label="订单编号"></el-table-column>

                <el-table-column prop="user_info" label="用户信息"></el-table-column>

                <el-table-column prop="name" label="收货人"></el-table-column>

                <el-table-column prop="mobile" label="联系方式"></el-table-column>

                <el-table-column prop="address" label="收货地址"></el-table-column>

                <el-table-column prop="goods_price" label="商品总价"></el-table-column>

                <el-table-column prop="coupon_price" label="优惠券抵扣"></el-table-column>

                <el-table-column prop="youhui_price" label="其他优惠"></el-table-column>

                <el-table-column prop="prom_type" label="订单活动类型"></el-table-column>

                <el-table-column label="是否分佣">
                    <template slot-scope="scope">
                        {{scope.row.is_distribut ? '是' : '否'}}
                    </template>
                </el-table-column>

                <el-table-column prop="device" label="下单设备"></el-table-column>

                <el-table-column prop="add_time" label="下单时间"></el-table-column>
            </el-table>
            <div style="text-align: right" class="block">
                <el-pagination @current-change="getList" :page-size="datas.limit"
                               layout="total, prev, pager, next" :total="datas.count">
                </el-pagination>
            </div>
        </div>
    </div>
</template>
<script>
    import * as api from "@/api";
    export default {
        name: "AgentOrder",
        props:['user_id'],
        data() {
            return {
                loading:false,
                datas:{},
            }
        },
        created(){
            this.getList();
        },
        methods: {
            getList(page=1){
                this.loading = true;
                api.post("/shop/admin.agent.agent/agentOrder", {
                    appToken: this.$store.getters.appToken,
                    user_id:this.user_id,
                    page:page,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },
        },
    }
</script>
<style scoped>

</style>
