<template>
    <!--获取-->
    <div >
        <div  v-loading="loading">
            <el-table :data="datas.list" style="width: 100%" size="small">
                <el-table-column prop="log_id" label="日志ID" width="100"></el-table-column>

                <el-table-column prop="money" label="提现金额">
                    <template slot-scope="scope">
                        ￥ {{scope.row.money}}
                    </template>
                </el-table-column>

                <el-table-column prop="money" label="当前剩余">
                    <template slot-scope="scope">
                        ￥ {{scope.row.now_money}}
                    </template>
                </el-table-column>

                <el-table-column prop="add_time" label="提现时间"></el-table-column>
            </el-table>
            <div style="text-align: right" class="block">
                <el-pagination @current-change="getList" :page-size="datas.limit"
                               layout="total, prev, pager, next" :total="datas.count">
                </el-pagination>
            </div>
        </div>
    </div>
</template>
<script>
    import * as api from "@/api";
    export default {
        name: "AgentWithdraw",
        props:['user_id'],
        data() {
            return {
                loading:false,
                datas:{},
            }
        },
        created(){
            this.getList();
        },
        methods: {
            getList(page=1){
                this.loading = true;
                api.post("/shop/admin.agent.agent/agentOrder", {
                    appToken: this.$store.getters.appToken,
                    user_id:this.user_id,
                    agent: 1,
                    page:page,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },
        },
    }
</script>
<style scoped>

</style>
