<template>
    <div>
        <top-nav title="推广业绩"></top-nav>
        <div class="wyu-main" v-loading="loading.loading">
            <div style="min-width: 1600px">
                <div class="wyu-block">
                    <el-row :gutter="10" style="height: 260px;padding: 20px">
                        <el-col :span="8">
                            <div class="user-view-item" style="height: 220px;padding: 50px 40px 40px 40px">
                                <el-col :span="8">
                                    <img width="120" height="120" :src="userDetail.face">
                                </el-col>
                                <el-col :span="8">
                                    <p style="color: #333333;font-weight:500;">
                                        {{userDetail.nickname}}(ID:{{$route.params.id}})</p>
                                    <div class="user-view-item-label"><span>{{userDetail.mobile}}</span></div>
                                    <div class="user-view-item-label"><span>性别：{{userDetail.sex_mean}}</span></div>
                                    <div class="user-view-item-label"><span>生日：{{userDetail.birthday}}</span></div>
                                </el-col>
                                <el-col :span="8" style="height: 100%; justify-content: flex-end; display: flex; align-items: center;">
                                    <div class="user-view-item-label"><span>综合排名：</span><span>第 <b style="font-size: xx-large;color: #F52A15;">{{agentDetail.my_rank}}</b> 名</span></div>

                                </el-col>
                            </div>
                        </el-col>
                        <el-col :span="4">
                            <div class="user-view-item" >
                                <div class="user-view-item-title"><span>顾问身份</span>
                                    <!--<span class="user-view-item-title-btn" v-if="userDetail.is_agent == 0" @click="dialog.agentDialog=true">操作</span>-->
                                    <el-button size="mini" style="float: right;margin-right: 20px" @click="dialog.agentDialog = true;">操作</el-button>
                                    <el-button type="text" size="mini" style="float: right;margin-right: 20px" @click="lock()">{{userDetail.agent_lock == 0 ? '锁定' : '解锁'}}</el-button>
                                </div>
                                <div class="user-view-item-label"><span>顾问：</span>
                                    <el-tag size="mini" type="success" v-if="userDetail.is_agent == 1">是</el-tag>
                                    <el-tag size="mini" type="info" v-if="userDetail.is_agent == 0">否</el-tag>
                                </div>
                                <div class="user-view-item-label">
                                    <span>分&nbsp;&nbsp;&nbsp;组：</span>
                                    <span>
                                        {{userDetail.agent_group_mean}}
                                    </span>

                                </div>

                            </div>
                        </el-col>
                        <el-col :span="4">
                            <div class="user-view-item" >
                                <div class="user-view-item-title"><span>推广人</span></div>
                                <div class="user-view-item-label"><span>直接推广者：</span><span
                                        style="display: inline-block;max-width: 130px" class="ellipsis-01">{{userDetail.pid_name}}</span><span
                                        v-if="userDetail.pid > 0">（ID:{{userDetail.pid}}）</span></div>
                                <div class="user-view-item-label"><span>推广者父级：</span><span
                                        style="display: inline-block;max-width: 130px" class="ellipsis-01">{{userDetail.pid2_name}}</span><span
                                        v-if="userDetail.pid > 0">（ID:{{userDetail.pid2}}）</span></div>
                            </div>
                        </el-col>
                        <el-col :span="4">
                            <div class="user-view-item" >
                                <div class="user-view-item-title"><span>注册信息</span></div>
                                <div class="user-view-item-label"><span>注册时间：</span><span>{{userDetail.add_time}}</span>
                                </div>
                                <div class="user-view-item-label"><span>注册设备：</span><span>{{userDetail.device}}</span>
                                </div>
                            </div>
                        </el-col>
                        <el-col :span="4">
                            <div class="user-view-item" >
                                <div class="user-view-item-title"><span>上次登录</span></div>
                                <div class="user-view-item-label">
                                    <span>登录时间：</span><span>{{userDetail.last_time}}</span></div>
                                <div class="user-view-item-label">
                                    <span>登录设备：</span><span>{{userDetail.last_device}}</span></div>
                            </div>
                        </el-col>



                        <el-col :span="4" style="margin-top: 20px">
                            <div class="user-view-item" >
                                <div class="user-view-item-title"><span>历史佣金</span></div>
                                <div class="user-view-item-label" style="color: red;">
                                    <span>￥</span><span style="font-size: xx-large"> {{agentDetail.history_agent_money}}</span></div>
                                <div class="user-view-item-label"></div>
                            </div>
                        </el-col>
                        <el-col :span="4" style="margin-top: 20px">
                            <div class="user-view-item" >
                                <div class="user-view-item-title"><span>可提现佣金</span></div>
                                <div class="user-view-item-label" style="color: red;">
                                    <span>￥</span><span style="font-size: xx-large"> {{agentDetail.agent_money}}</span></div>
                                <div class="user-view-item-label"></div>
                            </div>
                        </el-col>
                        <el-col :span="4" style="margin-top: 20px">
                            <div class="user-view-item" >
                                <div class="user-view-item-title"><span>推广注册</span></div>
                                <div class="user-view-item-label">
                                    <span>注册拉新：</span><span>{{agentDetail.agent_register}}(人)</span>
                                </div>
                                <div class="user-view-item-label">
                                    <span>推广发展下级：</span><span>{{agentDetail.agent_num}}(人)</span>
                                </div>
                            </div>
                        </el-col>
                        <el-col :span="4" style="margin-top: 20px">
                            <div class="user-view-item" >
                                <div class="user-view-item-title"><span>推广成果</span></div>
                                <div class="user-view-item-label">
                                    <span>推广用户订单：</span><span>{{agentDetail.agent_order}}(个)</span>
                                </div>
                                <div class="user-view-item-label">
                                    <span>推广开通VIP：</span><span>{{agentDetail.agent_vip}}(人)</span>
                                </div>
                            </div>
                        </el-col>
                    </el-row>
                </div>
            </div>
            <div class="wyu-block" style="margin-top: 20px">
                <agent-view-log :user_id="$route.params.id"></agent-view-log>
            </div>
        </div>


        <el-dialog title="顾问身份" :visible.sync="dialog.agentDialog" v-loading="loading.agentLoading" :modal-append-to-body="false" width="600px" center>
            <div class="wyu-tips wyu-tips-info" style="margin-bottom: 20px">
                这里只负责快速成为顾问、切换顾问等级
            </div>
            <el-form label-width="100px" style="padding: 0" v-loading="loading.couponLoading">
                <el-form-item label="顾问身份" >
                    <el-select size="mini" v-model="agentForm.agent_group" placeholder="选择顾问身份">
                        <el-option label="请选择" :value="-999"></el-option>
                        <el-option v-for="item in agentDetail.agent_group" :label="item.value" :value="item.key" :key="item.key"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item>
                    <el-button size="mini" @click="dialog.agentDialog = false;loading.agentLoading = false;">取 消</el-button>
                    <el-button size="mini" type="primary" @click="agentGroup()">确 定</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

    </div>
</template>

<script>
    import * as api from '@/api.js';
    import TopNav from "@/components/TopNav";
    import ImgUploads from "@/components/Photos";
    import AgentViewLog from "@/pages/agent/components/AgentViewLog";

    export default {
        name: "travelDepart",
        components: {AgentViewLog, ImgUploads, TopNav,},
        data: function () {
            return {
                loading: {
                    loading: false, //整体菊花
                    userInfoLoading: false,
                    lockLoading: false,
                    agentLoading: false,
                },
                //弹出控制
                dialog: {
                    agentDialog: false,

                },
                birthdaybm: false,  //生日保密
                userDetail: {},
                agentDetail: {},
                status: {},//所有状态集合
                agentForm: {
                    agent_group: -999,
                },
            };
        },
        created() {
            this.getUserDetail();
            this.getAgentDetail();
        },
        methods: {
            //锁定推广员
            agent() {
                this.loading.agentLoading = true;
                api.post("/shop/admin.user.user/agent", {
                    appToken: this.$store.getters.appToken,
                    user_id: this.$route.params.id,
                }).then(() => {
                    this.dialog.agentDialog = false;
                    this.loading.agentLoading = false;
                    this.getDetail();
                }).catch(() => {
                    this.dialog.agentDialog = false;
                    this.loading.agentLoading = false;
                });
            },


            agentGroup() {
                var that = this;
                that.loading.agentLoading = true;
                api.post('shop/admin.agent.agent/changeAgent', {
                    appToken: that.$store.getters.appToken,
                    user_id: that.$route.params.id,
                    agent_group: that.agentForm.agent_group,
                }).then(res => {
                    this.$message({
                        message: '操作成功',
                        type: 'success',
                    });
                    that.loading.agentLoading = false;
                    that.dialog.agentDialog = false;
                    this.getUserDetail();
                }).catch(() => {
                    that.loading.agentLoading = false;
                });
            },



            //获取详情
            getUserDetail() {
                var that = this;
                that.loading.loading = true;
                api.post('shop/admin.user.user/detail', {
                    appToken: that.$store.getters.appToken,
                    user_id: that.$route.params.id,
                }).then(res => {
                    that.loading.loading = false;
                    that.userDetail = res.detail;
                    this.status = res.status;
                }).catch(() => {
                    that.loading.loading = false;
                });
            },


            //获取详情
            getAgentDetail() {
                var that = this;
                that.loading.loading = true;
                api.post('shop/admin.agent.agent/viewAgent', {
                    appToken: that.$store.getters.appToken,
                    user_id: that.$route.params.id,
                }).then(res => {
                    that.loading.loading = false;
                    that.agentDetail = res;
                    this.status = res.status;
                }).catch(() => {
                    that.loading.loading = false;
                });
            },


            //锁定/解锁
            lock() {
                var that = this;
                that.loading.loading = true;
                api.post('shop/admin.agent.agent/lockagent', {
                    appToken: that.$store.getters.appToken,
                    user_id: that.$route.params.id,
                }).then(res => {
                    that.loading.loading = false;
                    that.userDetail.agent_lock = res;
                    this.status = res.status;
                }).catch(() => {
                    that.loading.loading = false;
                });
            }

        }
    };
</script>
<style>
    .user-view-item {
        height: 100px;
        padding-top: 15px;
        padding-left: 20px;
        background-color: #F8F9FB;
    }

    .user-view-item-title {
        font-size: 14px;
        font-weight: 500;
        color: #333333;
    }

    .user-view-item-label {
        font-size: 12px;
        margin-top: 12px;
    }

    .user-view-item-label span:nth-child(1) {
        color: #888888;
    }

    .user-view-item-title-btn {
        color: #005DE5;
        float: right;
        margin-right: 20px;
        cursor: pointer
    }
</style>
