<template>
    <!--获取佣金-->
    <div >
        <div  v-loading="loading">
            <el-table :data="datas.list" style="width: 100%" size="small">
                <!--<el-table-column type="expand">-->
                    <!--<template slot-scope="props">-->
                        <!--<table>-->
                            <!--<tr v-for="val in  props.row.goods">-->
                                <!--<td>{{val.goods_name}}</td>-->
                                <!--<td>——</td>-->
                                <!--<td>{{val.spec_key_name}}</td>-->
                            <!--</tr>-->
                        <!--</table>-->
                    <!--</template>-->
                <!--</el-table-column>-->

                <el-table-column width="70" prop="order_id" label="订单ID"></el-table-column>

                <el-table-column prop="order_sn" label="订单编号" width="130"></el-table-column>

                <el-table-column prop="user.user_id" label="消费者">
                    <template slot-scope="scope">
                        用户ID: {{scope.row.user.user_id}} | 昵称: {{scope.row.user.nickname}}
                    </template>
                </el-table-column>

                <el-table-column label="商品信息">
                    <template slot-scope="scope">
                        <p class="ellipsis-01">{{scope.row.goods_name}}</p>
                    </template>
                </el-table-column>

                <el-table-column label="获得佣金" width="100">
                    <template slot-scope="scope">
                        ￥ {{scope.row.money}}
                    </template>
                </el-table-column>

                <el-table-column label="全部佣金" width="100">
                    <template slot-scope="scope">
                        ￥ {{scope.row.commission}}
                    </template>
                </el-table-column>

                <el-table-column label="佣金比例" width="100">
                    <template slot-scope="scope">
                        {{scope.row.now_rate}} %
                    </template>
                </el-table-column>

                <el-table-column label="顾问等级" width="100">
                    <template slot-scope="scope">
                        {{scope.row.now_agent_group}}
                    </template>
                </el-table-column>

                <el-table-column label="顾问层级" width="100">
                    <template slot-scope="scope">
                        {{scope.row.agent_level}}
                    </template>
                </el-table-column>

                <el-table-column label="推广者">
                    <template slot-scope="scope">
                        用户ID: {{scope.row.puser.user_id}} | 昵称: {{scope.row.puser.nickname}}
                    </template>
                </el-table-column>

                <el-table-column label="受益者">
                    <template slot-scope="scope">
                        用户ID: {{scope.row.myuser.user_id}} | 昵称: {{scope.row.myuser.nickname}}
                    </template>
                </el-table-column>

                <el-table-column prop="add_time" label="分佣时间"></el-table-column>
            </el-table>
            <div style="text-align: right" class="block">
                <el-pagination @current-change="getList" :page-size="datas.limit"
                               layout="total, prev, pager, next" :total="datas.count">
                </el-pagination>
            </div>
        </div>
    </div>
</template>
<script>
    import * as api from "@/api";
    export default {
        name: "AgentCommission",
        props:['user_id'],
        data() {
            return {
                loading:false,
                datas:{},
            }
        },
        created(){
            this.getList();
        },
        methods: {
            getList(page=1){
                this.loading = true;
                api.post("/shop/admin.agent.agent/agentCommission", {
                    appToken: this.$store.getters.appToken,
                    user_id:this.user_id,
                    page:page,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },
        },
    }
</script>
