<template>
    <!--获取-->
    <div>
        <div v-loading="loading">
            <el-table :data="datas.list" style="width: 100%" size="small">
                <el-table-column prop="user_id" label="ID" width="100"></el-table-column>

                <el-table-column prop="mobile" label="联系方式" width="100"></el-table-column>

                <el-table-column prop="nickname" label="昵称" width="100"></el-table-column>

                <el-table-column label="头像" width="100">
                    <template slot-scope="scope">
                        <img :src="scope.row.face" width="50px"/>
                    </template>
                </el-table-column>

                <el-table-column label="性别" prop="sex" width="100"></el-table-column>

                <el-table-column label="分销" width="150">
                    <template slot-scope="scope">
                        <p v-if="scope.row.agent_group == 0">未开通</p>
                        <p v-if="scope.row.agent_group > 0">{{scope.row.agent_group_mean}}</p>
                    </template>
                </el-table-column>

                <el-table-column label="状态" width="150">
                    <template slot-scope="scope">
                        {{scope.row.is_lock ? '已锁定' : '正常'}}
                    </template>
                </el-table-column>

                <el-table-column prop="pid" label="上级"></el-table-column>

                <el-table-column prop="pid2" label="上上级"></el-table-column>

                <el-table-column prop="pid3" label="上上上级"></el-table-column>

                <el-table-column prop="device" label="设备" width="150"></el-table-column>

                <el-table-column prop="add_time" label="注册时间" width="150"></el-table-column>
            </el-table>
            <div style="text-align: right" class="block">
                <el-pagination @current-change="getList" :page-size="datas.limit"
                               layout="total, prev, pager, next" :total="datas.count">
                </el-pagination>
            </div>
        </div>
    </div>
</template>
<script>
    import * as api from "@/api";
    export default {
        name: "AgentRegister",
        props:['user_id'],
        data() {
            return {
                loading:false,
                datas:{},
            }
        },
        created(){
            this.getList();
        },
        methods: {
            getList(page=1){

                this.loading = true;
                api.post("/shop/admin.agent.agent/agentRegister", {
                    appToken: this.$store.getters.appToken,
                    user_id: this.user_id,
                    page: page,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },
        },
    }
</script>
<style scoped>

</style>
